@charset "UTF-8";
@import "~quill/dist/quill.core.css";
@import "~quill/dist/quill.bubble.css";
@import "~quill/dist/quill.snow.css";
@import "~quill-emoji/dist/quill-emoji.css";
body {
  scroll-behavior: smooth;
}

.spinner {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1000;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
}

.chat-header h3 {
  font-size: 18px;
}
.chat-header .control .batch_list {
  width: 200px;
  padding-right: 0.5rem;
}

.chat-body {
  margin: 1.5rem 0;
  height: 430px;
  overflow-y: scroll;
}

.chat-footer {
  padding: 0.8rem;
  background: rgba(168, 167, 167, 0.247);
}
.chat-footer .chat-control .attachment {
  padding: 0.2rem;
}
.chat-footer .chat-control .attachment .file label {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #15c39a;
}
.chat-footer .chat-control .send {
  width: 50px;
  height: 50px;
}
.chat-footer .chat-control .send button {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 50%;
  background-color: #0a80ff;
  color: white;
  font-size: 16px;
}

.batch-list {
  max-height: 60px;
  overflow-x: scroll;
  overflow-y: hidden;
  border-radius: 15px;
  margin-bottom: 1rem;
}
.batch-list ul {
  height: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  margin-top: 0.5rem;
}
.batch-list ul li {
  min-width: 40px;
}

.batch-list::-webkit-scrollbar {
  width: 0;
}

.post-comm {
  width: 75%;
}

.align-self-start {
  background-color: #0a80ff !important;
  color: #fff !important;
}
.align-self-start .comment-container .comment-author {
  color: #fff !important;
}
.align-self-start .comment-container .comment-author .comment-date {
  color: #fff !important;
}

.align-self-end {
  background-color: #f3f6fa;
  color: #000;
}
.align-self-end .comment-container .comment-author {
  color: #000;
}
.align-self-end .comment-container .comment-author .comment-date {
  color: #000;
}

.badge {
  padding: 10px 15px;
}

.email-date {
  min-width: 2em;
  min-height: 1.5em;
  border-radius: 50%;
  text-align: center;
  color: #fff;
  background-color: #075e54;
}

.warnning {
  text-align: center;
  line-height: 25px;
  color: #ee6e83;
}

.mr-2 {
  margin-right: 0.5rem;
}

.files-count {
  position: absolute !important;
  left: 100% !important;
  top: 5px !important;
  z-index: 1000;
  color: #fff;
  background: red;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
}

.z-1000 {
  z-index: 1000;
}

.mr-3 {
  margin-right: 1rem;
}

blockquote {
  background: #f9f9f9;
  border-left: 10px solid #ccc;
  margin: 1.5em 10px;
  padding: 0.5em 10px;
  quotes: "" "" "" "";
}

blockquote:before {
  color: #ccc;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}

blockquote p {
  display: inline;
}

.priority {
  color: #e75252;
}

.file input[type=file] {
  display: none;
}
.file p {
  display: block;
  padding-left: 0.3rem !important;
}
.file label {
  width: 165px;
  height: 35px;
  background-color: #d0d0d0db;
  cursor: pointer;
  padding: 1rem;
  border-radius: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  color: #3030308c;
}
.file label span:nth-child(2) {
  padding: 0.1em 0.6em;
  border-radius: 50%;
  background-color: #15c39a;
  font-size: 12px;
  color: #fff;
}